import Page from 'components/Page';
import React,{useState,useEffect} from 'react';
import {
  Col,InputGroup,InputGroupAddon,Input,FormGroup,Label,Button,
  Row} from 'reactstrap';
import {
  FaArrowCircleLeft,FaSearch,FaKey,
  FaBars} from 'react-icons/fa';
import {Link} from 'react-router-dom';
import Typography from '../components/Typography';
import bn from 'utils/bemnames';
const bem = bn.create('page');
const RechargePage = (props) => {
  const [budget,setBudget]=useState(JSON.parse(localStorage.getItem('auth')).user.budget);
  const [amount,setAmount]=useState('');
  const apply=()=>{
    (async ()=>{
      const response=await fetch("/api/recharge", {
        "method": "POST",
        "headers": {
          "content-type": "application/json",
          "Authorization":JSON.parse(localStorage.getItem('auth')).userToken

        },
        body:JSON.stringify({amount})
      });
      const data=await response.json();
      if(response.status==200){
        var f = document.createElement("form");
        f.setAttribute('method',"post");
        f.setAttribute('action',data.url);
        const sortedkeys = Object.keys(data.postData);
        for (var k = 0; k < sortedkeys.length; k++) {
          var i = document.createElement("input"); //input element, text
          i.setAttribute('type',"text");
          i.setAttribute('name',sortedkeys[k]);
          i.setAttribute('value',data.postData[sortedkeys[k]]);
          f.appendChild(i);
         
        }
        document.getElementsByTagName('body')[0].appendChild(f);
        f.submit();

        
                
      }           
      else
        alert(data.error);
    })();  
  };
  useEffect(() => {
    (async ()=>{
      const response=await fetch("/api/budget", {
        "method": "GET",
        "headers": {
          "content-type": "application/json",
          "Authorization":JSON.parse(localStorage.getItem('auth')).userToken

        }
      });
      const data=await response.json();
      var tmp=JSON.parse(localStorage.getItem('auth'));
      tmp.user.budget=data.budget;
      localStorage.setItem('auth',JSON.stringify(tmp));
      setBudget(data.budget);
    })();      
  },[]);
  return (
    <Page title={(<><Link to="/my"><Typography type="h4" className={bem.e('title')}><FaArrowCircleLeft /> Recharge</Typography></Link><Link color="link" to='/rechargeList' style={{"padding":"20px"}}><FaBars /></Link></>)} className="MyPage"  >

      <Row>       
        <Col md={12} style={{textAlign:'center'}} className={'mt-3'}>
        <h3>Balance: ₹ {budget}</h3>
        </Col> 
        <Col xl={12} lg={12} md={12}>
          <InputGroup>
            <InputGroupAddon addonType="prepend"><span className="input-group-text">₹</span></InputGroupAddon>
            <Input value={amount} type="number" placeholder="Enter Recharge amount" onChange={(e)=>{setAmount(e.target.value)}}/>
          </InputGroup>
        </Col>
        <Col xl={12} lg={12} md={12}>
          <Button color="primary" className={'ml-3 mr-3 mt-2'} style={{width:'80px', padding:"4px 4px"}} onClick={()=>setAmount(100)} >₹ 100</Button>
          <Button color="primary" className={'ml-3 mr-3 mt-2'} style={{width:'80px', padding:"4px 4px"}} onClick={()=>setAmount(1000)} >₹ 1000</Button>
          <Button color="primary" className={'ml-3 mr-3 mt-2'} style={{width:'80px', padding:"4px 4px"}} onClick={()=>setAmount(2000)} >₹ 2000</Button>
          <Button color="primary" className={'ml-3 mr-3 mt-2'} style={{width:'80px', padding:"4px 4px"}} onClick={()=>setAmount(5000)} >₹ 5000</Button>
          <Button color="primary" className={'ml-3 mr-3 mt-2'} style={{width:'80px', padding:"4px 4px"}} onClick={()=>setAmount(10000)} >₹ 10000</Button>
          <Button color="primary" className={'ml-3 mr-3 mt-2'} style={{width:'80px', padding:"4px 4px"}} onClick={()=>setAmount(20000)} >₹ 20000</Button>
        </Col>
        <Col md={12} style={{textAlign:'center'}} className={'mt-3'}>
          <Button onClick={apply} color="success"> Recharge </Button>
        </Col>         
      </Row>
    </Page>
  );
};

export default RechargePage 
